import React, { Component } from 'react'

import Grid from '@material-ui/core/Grid'

import { Button, Typography} from '@material-ui/core'
import Fastfood from '@material-ui/icons/Fastfood'
import { withStyles } from '@material-ui/styles'
import CasinoIcon from '@material-ui/icons/Casino';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Header from '../Header'
import dinnertimelogo from './img/dt.png'


const useStyles = theme => ({
    typographyStyles: {
        flex: 1
    },
		paper: {
		 marginTop: theme.spacing(8),
		 display: 'flex',
		 flexDirection: 'column',
		 alignItems: 'center',
	 },
	 form: {
		 width: '100%', // Fix IE 11 issue.
		 marginTop: theme.spacing(1),
	 },
	 submit: {
		 margin: theme.spacing(3, 0, 2),
	 },
	 root: {
		 flexGrow: 1,
	 },
});


export class FindFood extends Component {

    componentDidMount() {
        let currentComponent = this;
        if ("geolocation" in navigator) {
          console.log("Available");
          navigator.geolocation.getCurrentPosition(function(position) {
            console.log("Latitude is :", position.coords.latitude);
            console.log("Longitude is :", position.coords.longitude);
            currentComponent.props.updateLatitude(position.coords.latitude);
            currentComponent.props.updateLongitude(position.coords.longitude);
          });
        } else {
          console.log("Not Available");
        }
    }

    continue = e => {
        e.preventDefault()
        this.props.nextStep()
    }

    prevStep = e => {
        this.props.prevStep()
    }

    goToConfirm = e => {
        e.preventDefault()
        this.props.goToConfirm()
    }

    goToFavoritesList = e => {
        e.preventDefault()
        this.props.goToFavoritesList()
    }

    goToFindFood = e => {
        e.preventDefault()
        this.props.goToFindFood()
    }

		goHome = e => {
			this.props.goHome()
		}

		goToLeave = e => {
			this.props.goToLogOut()
		}

		getFavorites = e => {
			this.props.goToFavoritesList()
		}

    render() {
			const {classes} = this.props;
        const { handleChange} = this.props

        return (
					<Grid container
							direction='column'
					>
					<Header
					goToLogOut={this.goToLeave}
					goHome={this.goHome}
					getFavorites={this.getFavorites}
					/>
            <Grid container
                direction='column'
                justify='center'
                alignItems='center'
                spacing={3}
								style={{height: '80vh' }}
            >
                <Grid item />


									<Container component="main" maxWidth="md">
								<CssBaseline />
							 <div className={classes.paper}>
                         <Typography variant='h3' align='center' className={useStyles.typographyStyles}>
                            It's Dinner Time!
                        </Typography>
                        <Button
                            variant='contained'
                            color="secondary"
                            fullWidth={true}
                            onClick={this.continue}
                            startIcon={<Fastfood />}
														className={classes.submit}
                        >
                            Dinner Time
                        </Button>



                        <Button
                            variant='contained'
                            color="secondary"
                            onChange={handleChange('foodTypes')}
                            fullWidth={true}
                            onClick={this.goToConfirm}
                            startIcon={<CasinoIcon />}
														className={classes.submit}
                        >
                            Random
                        </Button>

                        <Button
                            variant='contained'
                            color="secondary"
                            fullWidth={true}
                            onClick={this.goToFavoritesList}
                            startIcon={<FavoriteIcon />}
														className={classes.submit}
                        >
                            View Favorites
                        </Button>


                        <Button
                            variant='contained'
                            color="primary"
                            fullWidth={true}
                            onClick={this.prevStep}
                            startIcon={<ExitToAppIcon />}
														className={classes.submit}
                        >
                            Log Out
                        </Button>

											</div>
												</Container>

            </Grid>
					</Grid>
        )
    }
}

export default withStyles(useStyles)(FindFood)
